import React, { useState } from "react";

const Form = ({ addTodo }) => {
    const [value, setValue] = useState('')

    const handleSubmit = (e) => {
        e.preventDefault()
        addTodo(value)
    }

    const handleInputTxt = (e) => {
        setValue(e.target.value)
    }

    return (
        <form onSubmit={handleSubmit}>
            <input type="text" onChange={handleInputTxt} />
        </form>
    )
}

export default Form
